.listing-info {
    .title-section {
        margin-bottom: 30px;

        .back-button {
            color: $border-light-gray;
            display: inline-flex;
            align-items: center;
            font-size: 15px;
            margin-bottom: 30px;

            img {
                width: 25px;
                margin-right: 10px;
            }

            &:hover {
                color: $primary-color;
            }
        }

        .title {
            margin-bottom: 20px;
        }

        .sub-title-align {
            display: flex;
            flex-direction: column;
        }

        .sub-title {
            color: $orange;
            text-transform: uppercase;
            font-size: 15px;
            margin-bottom: 0;

            @extend .Brown-Bold-web;
        }

        .link {
            display: inline-block;
            font-size: 14px;
            color: $border-light-gray;
            text-decoration: underline;

            &:hover {
                color: $primary-color;
            }
        }
    }

    &__slider {
        .carousel-card {
            padding-left: 0;
            padding-right: 0;
        }

        .carousel-card .slider-item:before {
            padding-top: 83.42%;
        }

        .carousel-card .carousel-arrows .slick-prev {
            left: 15px;
        }

        .carousel-card .carousel-arrows .slick-next {
            right: 15px;
        }

        .button {
            position: absolute;
            left: 0;
            bottom: 50px;
            width: 100%;
            background: rgba(0, 0, 0, 0.44);
            height: 50px;
            font-size: 14px;

            &:hover {
                background: $black;
            }
        }

        .icon-zoom {
            position: absolute;
            left: 15px;
            top: 20px;
            width: 25px;
            cursor: pointer;

            img {
                width: 100%;
            }
        }
    }

    &__details {
        margin-top: 20px;
    }

    &__details .info-section {
        .info-item {
            margin-bottom: 30px;
        }

        .info-item .title {
            color: $border-light-gray;
            text-transform: uppercase;
            margin-bottom: 20px;
            padding-bottom: 15px;
            border-bottom: 1px solid $border-light-gray;
            font-size: 15px;

            @extend .Brown-Bold-web;
        }

        .info-item .list {
            margin: 0;

            li {
                list-style: none;
                list-style-type: none;
                font-size: 14px;
            }

            li .number {
                margin-left: 5px;
            }
        }

        .info-item .icon-list {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            margin-top: 30px;
        }

        .info-item .icon-list li {
            display: flex;
            align-items: center;
            font-size: 20px;
            font-weight: bold;
        }

        .info-item .icon-list li img {
            margin-right: 10px;
            height: 20px;
        }

        .info-item .address {
            font-style: inherit;
            font-size: 14px;
            text-decoration: underline;
        }

        .info-item p {
            font-size: 14px;
            margin-bottom: 0;
        }

        .price-item {
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-bottom: 1px solid $border-light-gray;
            border-top: 1px solid $border-light-gray;
            padding: 8px 0;

            .title {
                margin-bottom: 0;
                padding-bottom: 0;
                border-bottom: 0;
            }

            .price {
                color: $orange;
                position: relative;
                font-size: 30px;
                line-height: 1;

                @extend .GT-Sectra-Display-web;
            }

            .price:before {
                content: '$';
                color: $orange;
                display: inline-block;
            }
        }
    }

    @include breakpoint(medium) {
        .carousel-card .slider-item:before {
            padding-top: 56%;
        }

        .title-section {
            .title {
                font-size: 40px;
            }

            .sub-title-align {
                flex-direction: row;
                align-items: flex-end;
            }

            .sub-title {
                font-size: 18px;
                margin-right: 20px;
            }

            .link {
                font-size: 15px;
            }
        }

        &__details .info-section .info-item .title,
        &__details .info-section .info-item .list li,
        &__details .info-section .info-item .address,
        &__details .info-section .info-item p {
            font-size: 16px;
        }

        &__details .info-section .info-item .icon-list {
            max-width: 200px;
        }

        &__details .info-section .info-item .icon-list li {
            width: 50%;
            margin-bottom: 20px;
        }

        &__details .info-section .price-item {
            padding: 12px 0;

            .price {
                font-size: 34px;
            }
        }
    }

    @include breakpoint(large) {
        &__slider {
            .carousel-card .slider-item:before {
                padding-top: 83.42%;
            }

            .carousel-card .carousel-arrows .slick-prev {
                left: 20px;
            }

            .carousel-card .carousel-arrows .slick-next {
                right: 20px;
            }

            .icon-zoom {
                left: 20px;
            }
        }

        .title-section {
            .title {
                font-size: 50px;
            }

            .sub-title {
                font-size: 20px;
            }
        }

        &__details {
            margin-top: 0;
        }

        &__details .info-section .info-item .title,
        &__details .info-section .info-item .list li,
        &__details .info-section .info-item .address,
        &__details .info-section .info-item p {
            font-size: 18px;
        }

        &__details .info-section .info-item .icon-list {
            margin-top: 40px;
        }

        &__details .info-section .price-item {
            padding: 20px 0;

            .price {
                font-size: 38px;
            }
        }
    }

    @include breakpoint(xlarge) {
        .title-section {
            .title {
                font-size: 60px;
            }
        }

        &__details .info-section .info-item .title,
        &__details .info-section .info-item .list li,
        &__details .info-section .info-item .address,
        &__details .info-section .info-item p {
            font-size: 20px;
        }

        &__details .info-section .price-item {
            margin-top: 40px;
        }
    }
}
