.listing-contact {
    margin-top: 50px;

    .wrapper {
        background: $form-bg;
        padding: 35px 0;
        position: relative;

        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: -35px;
            width: 100vw;
            height: 100%;
            background: $form-bg;
        }
    }

    &__show-home {
        position: relative;
        padding: 60px 35px;
        overflow: hidden;
        background: $black;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-bottom: 35px;

        .bg-image {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            z-index: 0;
            opacity: 0.8;

            @include bg-image;
        }

        .info {
            position: relative;
            margin-bottom: 35px;
        }

        .info p {
            color: $white;

            &:last-child {
                margin-bottom: 0;
            }
        }

        .button {
            position: relative;
            background: $dark-blue;

            &:hover {
                background: $orange;
            }
        }
    }

    &__content {
        position: relative;

        .title {
            margin-bottom: 35px;
        }

        .info {
            margin-bottom: 35px;

            p:last-child {
                margin-bottom: 0;
            }
        }

        .button-align {
            position: relative;
            padding-bottom: 25px;
            border-bottom: 1px solid $border-light-gray;
            margin-bottom: 25px;

            .button {
                width: 100%;
            }
        }

        .guideline {
            position: relative;
            margin-bottom: 30px;

            p {
                font-size: 15px;
            }
        }
    }

    &__form {
        position: relative;

        .form-align {
            overflow: hidden;
        }
    }

    @include breakpoint(medium) {
        margin-top: 0;

        .wrapper {
            padding: 35px 25px;

            &:before {
                display: none;
            }
        }
    }

    @include breakpoint(large) {
        //margin-top: -100px;

        .wrapper {
            padding: 35px;
        }
    }
}
