.select2-selection {
    height: 50px;
    font-size: 13px;
    font-family: 'Open Sans', sans-serif;
    border-radius: 0;
    display: block;
}

.js-select-multiple {
    display: none;
    height: 0 !important;
    overflow: hidden;
}

.select2-hidden-accessible {
    display: none;
    height: 0 !important;
    overflow: hidden;
}

.select2-selection--multiple {
    width: 100%;
    min-height: 50px;
}

.select2-selection__choice {
    font-size: 15px;
    line-height: 1.333;
    padding-right: 20px;
    padding-left: 15px;
    color: $black;
    border: none;
    font-family: 'brown';
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.select2-selection__choice__remove {
    position: absolute;
    left: 0;
    font-size: 20px;
    line-height: 1;
    cursor: pointer;
    color: $border-light-gray;

    @include transition;

    &:hover {
        color: $red;
    }
}

.select2-search--inline .select2-search__field {
    font-size: 15px;
    line-height: 1.333;
    color: $black;
    margin-bottom: 0;
    border: 0;
    box-shadow: none;
    background: transparent;
    height: auto;
    padding: 0 10px 0 0;
}

.select2-container:hover,
.select2-container:focus,
.select2-container:active,
.select2-selection:hover,
.select2-selection:focus,
.select2-selection:active {
    outline-color: transparent;
    outline-style: none;
}

.select2-container.select2-container--open {
    min-width: 250px;
    width: auto;
    background: $black;
    padding: 10px 0;
    margin: 30px 0 0;
    z-index: 100;
}

.select2-results__option {
    padding: 5px 15px;
    cursor: pointer;
}

.select2-results__options {
    margin: 0;
}

.select2-results__options li {
    display: block;
}

.select2-selection__rendered {
    margin: 0;
    display: inline-flex;
    flex-wrap: wrap;

    li {
        list-style: none;
        list-style-type: none;
    }
}

.select2-selection__arrow {
    display: none;
}

.select2-results__option--highlighted {
    background-color: $red;
}

.select2-dropdown {
    border-radius: 0 !important;
    border: none !important;
    margin-top: 4px !important;
    width: 366px !important;
}

.select2-results__option {
    font-family: 'brown';
    font-size: 15px;
    line-height: 1.333;
    color: $white;
}

.select2-search__field {
    font-family: 'Open Sans', sans-serif;
    color: $border-light-gray;
    font-size: 13px;
    padding-left: 8px;
    width: 120px !important;
}

.select2-selection__placeholder {
    color: #c4c4c4 !important;
}
