.sidebar-accordion.accordion {
    .accordion-item {
        margin-bottom: 0;
    }

    .accordion-title {
        padding: 20px 60px 20px 0;
        border-left: 0;
        border-right: 0;
        color: $black;

        &:before {
            content: '';
            right: 0;
            width: 18px;
            height: 18px;
            background-image: url("../images/png/down-arrow-dropdown.png");
            background-position: center;
            background-size: 90%;
            background-repeat: no-repeat;
        }

        .text {
            position: absolute;
            right: 25px;
            top: 50%;
            transform: translateY(-50%);
            font-size: 16px;
            text-transform: capitalize;
            font-weight: 400;
        }
    }

    .is-active > .accordion-title:before {
        background-image: url("../images/png/up-arrow-dropdown.png");
    }

    .accordion-content {
        padding: 0 0 20px;
        border: 0;
    }

    .accordion-title:hover,
    .accordion-title:focus {
        background-color: transparent;
    }

    @include breakpoint(large) {
        .accordion-title {
            border: 0;
            //padding: 15px 0;
            padding: 25px 0 10px;
            pointer-events: none;

            &:before {
                display: none;
            }
        }

        .accordion-item:first-child {
            .accordion-title {
                padding-top: 0;
            }
        }

        .accordion-content {
            padding-bottom: 0;
            display: block !important;
        }
    }
}
