.block-icon .icon {
    margin: 0 0.5rem;

    @include breakpoint(xlarge up) {
        margin: 0 1rem;
    }
}

.block-icon.narrow .icon {
    @include breakpoint(xlarge up) {
        margin: 0 0.5rem;
    }
}

.block-icon .icon img {
    max-width: 25px;
    max-height: 25px;
    vertical-align: bottom;
}

.block-icon .icon-count {
    vertical-align: bottom;
    line-height: 1;
    padding-left: 0.2rem;
    font-weight: $global-weight-bold;
}

.block-icon .icon p {
    margin-top: 1rem;
}

.block-icon .title-count {
    display: block;

    @include breakpoint(xlarge up) {
        display: inline;
    }
}
