/* Slider */
.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}

.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0 !important;
    line-height: 0;

    &:focus {
        outline: none;
    }
}

.slick-list.dragging {
    cursor: pointer;
    cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    margin-left: auto;
    margin-right: auto;

    &:before,
    &:after {
        content: "";
        display: table;
    }

    &:after {
        clear: both;
    }
}

.slick-track {
    .slick-loading & {
        visibility: hidden;
    }
}

.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    display: none;
}

.slick-slide img {
    display: block;
}

.slick-slide {
    [dir="rtl"] & {
        float: right;
    }

    .slick-initialized & {
        display: block;
    }

    .slick-loading & {
        visibility: hidden;
    }

    .slick-vertical & {
        display: block;
        height: auto;
        border: 1px solid transparent;
    }
}

.slick-slide.slick-loading img {
    display: none;
}

.slick-slide.dragging img {
    pointer-events: none;
}

.slick-arrow.slick-hidden {
    display: none;
}

.slick-next,
.slick-prev {
    position: absolute;
    top: 0;
    font-size: 0;
    line-height: 0;
    width: 20px;
    height: 20px;
    display: block;
    z-index: 10;
    cursor: pointer;
    background-repeat: no-repeat !important;
    background-size: cover !important;
    background-position: center !important;
}

.slick-next {
    background: url("../images/png/next.png");
    right: 15px;
}

.slick-prev {
    background: url("../images/png/prev.png");
    left: 15px;
}

.slick-dots {
    list-style: none;
    margin: 0;
    padding-top: 0;
    display: flex;
    justify-content: center;
}

.slick-dots li {
    margin: 0 5px;
    line-height: 0;
}

.slick-dots button {
    width: 13px;
    height: 13px;
    display: inline-block;
    border: 0;
    background: $border-light-gray;
    border-radius: 50%;
    overflow: hidden;
    text-indent: -9999px;
    cursor: pointer;

    @include transition;

    &:hover {
        background: $orange;
    }
}

.slick-dots .slick-active button {
    background: $orange;
}

@include breakpoint(large) {
    .slick-next,
    .slick-prev {
        width: 38px;
        height: 38px;
    }
}

@include breakpoint(xlarge) {
    .slick-next {
        right: 30px;
    }

    .slick-prev {
        left: 30px;
    }
}
