.pathway-card {
    position: relative;
    margin-bottom: 20px;
}

a.pathway-card {
    &:before {
        content: '';
        position: absolute;
        top: -15px;
        left: -15px;
        width: calc(100% + 30px);
        height: calc(100% + 16px);
        background: rgba(191, 189, 186, 0.4);

        @extend .fade-out;
    }

    &:hover,
    &:focus {
        &:before {
            @extend .fade-in;
        }
    }
}

.pathway-card__image {
    position: relative;
    overflow: hidden;
    display: block;

    &:before {
        content: '';
        width: 100%;
        padding-top: 85.33%;
        display: block;
    }
}

.pathway-card__content {
    position: relative;
    padding: 25px 0;
}

.pathway-card .image {
    @extend .absolute-100;

    @include bg-image;
    @include transition-image;
}

.pathway-card .title {
    font-size: 20px;
    text-align: center;
    text-transform: uppercase;
    color: $black;

    @extend .Brown-Bold-web;
}

.pathway-card .info p {
    text-align: center;
    color: $black;

    &:last-child {
        margin-bottom: 0;
    }
}

.pathway-card--left-align .pathway-card__image:before {
    padding-top: 76.923%;
}

.pathway-card--left-align .title {
    text-align: left;
    text-transform: inherit;
    font-family: 'GT-Sectra-Display';
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
}

.pathway-card--left-align .info p {
    text-align: left;
}

.pathway-card--left-align .pathway-card__content .info {
    margin-bottom: 20px;
}

.pathway-card--left-align .pathway-card__image {
    &:before {
        background-color: rgba(191, 189, 186, 0.4);
        background-image: url("../images/png/placeholder-logo.png");
        background-position: center;
        background-repeat: no-repeat;
        background-size: 120px;
    }
}

.pathway-card .link {
    text-decoration: underline;
    color: $black;

    @include transition;

    &:hover {
        color: $primary-color;
    }
}

@include breakpoint(large) {
    .pathway-card__image {
        &:before {
            padding-top: 86.195%;
        }
    }

    .pathway-card--left-align {
        margin-bottom: 25px;
    }

    .pathway-card--left-align .title {
        font-size: 25px;
    }
}

@include breakpoint(xlarge) {
    .pathway-card--left-align .title {
        font-size: 30px;
    }
}

@include breakpoint(xxlarge) {
    .pathway-card {
        max-width: 300px;
    }

    .pathway-card--left-align {
        max-width: none;
    }
}
