.mfp-wrap {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 100px 30px 50px;
    z-index: 999;
    background: rgba(0, 0, 0, 0.8);

    .mfp-close {
        position: absolute;
        right: 0;
        top: -40px;
        color: white;
        z-index: 999;
        font-size: 35px;
        cursor: pointer;

        &:hover {
            color: $primary-color;
        }
    }

    .mfp-iframe-scaler {
        position: relative;
        max-width: 1200px;
        margin-left: auto;
        margin-right: auto;

        &:before {
            content: '';
            width: 100%;
            padding-top: 56.25%;
            display: block;
        }
    }

    iframe {
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
    }
}
