.sidebar .mobile-filters-box .close-button {
    padding: 0;
    margin-bottom: 1rem;
    font-size: 2rem;
    cursor: pointer;

    @include transition;

    &:hover {
        color: $primary-color;
    }
}

.sidebar .mobile-filters-box .position-right {
    width: 100%;
    transform: translateX(100%) !important;
}

.sidebar .mobile-filters-box .position-right.is-transition-overlap.is-open {
    transform: translate(0, 0) !important;
}

.sidebar .close-button {
    position: absolute;
    right: 35px;
    top: 120px;
    line-height: 1;
}

.sidebar .dropdown-wrapper,
.sidebar .checkbox-wrapper {
    margin-bottom: 15px;
}

.sidebar .dropdown-wrapper .small-title,
.sidebar .checkbox-wrapper .small-title {
    font-size: 15px;
    font-weight: bold;
    text-transform: uppercase;
    display: flex;
    align-items: center;

    @extend .Brown-Bold-web;
}

.sidebar .dropdown-wrapper .small-title img,
.sidebar .checkbox-wrapper .small-title img {
    max-width: 100%;
    width: 24px;
    margin-right: 10px;
    display: none;
}

.sidebar .dropdown-wrapper.small-icon .small-title img,
.sidebar .checkbox-wrapper.small-icon .small-title img {
    width: 12px;
}

.sidebar .checkbox-wrapper ul {
    margin: 0;
}

.sidebar .checkbox-wrapper ul li {
    list-style: none;
    list-style-type: none;
    margin-bottom: 10px;

    &:last-child {
        margin-bottom: 0;
    }
}

.sidebar .dropdown-wrapper--wide .dropdown {
    width: 100% !important;
}

.sidebar .dropdown-wrapper .dropdown-item {
    display: flex;
    align-items: center;
}

.sidebar .dropdown-wrapper .dropdown-item .dropdown {
    width: calc(50% - 35px);
    position: relative;

    &:before {
        content: '';
        position: absolute;
        right: 20px;
        top: 50%;
        transform: translate(0, -50%);
        width: 10px;
        height: 18px;
        background-size: 10px;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url("../images/png/icon-select-arrows.png");
    }
}

.sidebar .dropdown-wrapper .dropdown-item .text {
    width: 70px;
    text-align: center;
    padding: 5px 15px;
    display: block;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 14px;
}

.sidebar .dropdown-wrapper .dropdown-item .dropdown select {
    margin-bottom: 0;
    border-color: $border-light-gray;
    height: 50px;
    padding: 0 0 0 20px;

    &:focus {
        outline: auto;
    }
}

.sidebar .wrapper {
    padding: 170px 35px 35px;
}

.sidebar .button-align {
    margin-bottom: 30px;
    margin-top: 20px;
}

.sidebar .button-align .button {
    height: 50px;
}

.sidebar .button-align .hollow {
    text-transform: inherit;
    padding-left: 10px;
    padding-right: 10px;
    color: $border-light-gray;
}

.sidebar .checkbox-wrapper ul li > {
    [type="checkbox"]:not(:checked) + label:after {
        opacity: 0;
        -webkit-transform: scale(0);
        transform: scale(0);
    }

    [type="checkbox"]:checked + label:after,
    [type="checkbox"]:not(:checked) + label:after {
        content: '';
        width: 18px;
        height: 18px;
        background: url(../images/svg/icon-check.svg) no-repeat;
        position: absolute;
        left: 0;
        top: 6px;
        -webkit-transition: all 0.2s ease;
        transition: all 0.2s ease;
        outline: none;
    }

    [type="checkbox"]:checked + label:before {
        background: #fff;
    }

    [type='checkbox'] + label {
        margin-left: 0;
    }

    [type="checkbox"]:checked,
    [type="checkbox"]:not(:checked) {
        position: absolute;
        left: -9999px;
    }

    [type="checkbox"]:checked + label,
    [type="checkbox"]:not(:checked) + label {
        position: relative;
        padding-left: 25px;
        cursor: pointer;
        line-height: 24px;
        display: inline-block;
        color: $black;
        font-size: 16px;
        outline: none;
    }

    [type="checkbox"]:checked + label:before,
    [type="checkbox"]:not(:checked) + label:before {
        content: '';
        position: absolute;
        left: 0;
        top: 6px;
        width: 18px;
        height: 18px;
        background: url(../images/svg/icon-box.svg) no-repeat;
        outline: none;
    }
}

.sidebar .button-align .button {
    width: calc(50% - 10px);

    &:first-child {
        margin-right: 10px;
    }

    &:last-child {
        margin-left: 10px;
    }
}

@include breakpoint(374px down) {
    .sidebar .dropdown-wrapper .small-title,
    .sidebar .checkbox-wrapper .small-title {
        font-size: 14px;
    }
}

@include breakpoint(large) {
    .sidebar .dropdown-wrapper .small-title,
    .sidebar .checkbox-wrapper .small-title {
        font-size: 15px;
    }

    .sidebar .dropdown-wrapper .small-title img,
    .sidebar .checkbox-wrapper .small-title img {
        display: block;
    }

    .sidebar .dropdown-wrapper .dropdown-item .dropdown select {
        height: 40px;
    }

    .sidebar .wrapper {
        padding: 0;
    }

    .sidebar .button-align .button {
        height: 35px;
    }

    .sidebar .button-align .hollow {
        color: $black;
    }

    .sidebar .dropdown-wrapper .dropdown-item .dropdown {
        width: calc(50% - 25px);
    }

    .sidebar .dropdown-wrapper .dropdown-item .text {
        width: 50px;
        padding: 5px;
    }

    .sidebar .price .dropdown select {
        font-size: 14px;
        padding: 0 0 0 10px !important;
    }

    .sidebar .price .dropdown {
        &:before {
            right: 10px !important;
        }
    }

    .sidebar .mobile-filters-box .position-right {
        transform: translateX(0) !important;
    }

    .sidebar .mobile-filters-box .position-right.is-transition-overlap.is-open {
        transform: translate(0, 0) !important;
    }
}
