input[type='text'],
[type='password'],
[type='date'],
[type='datetime'],
[type='datetime-local'],
[type='month'],
[type='week'],
[type='email'],
[type='number'],
[type='search'],
[type='tel'],
[type='time'],
[type='url'],
[type='color'],
textarea,
select {
    box-shadow: none; // Removes Foundation's default box-shadow, this is optional
}

// Styling UserForm module's Action (Submit) buttons
form [type='submit'],
form [type='button'] {
    @include button(true, $primary-color, $primary-color, #fff, solid);

    width: auto;
    display: inline-block;
    margin-right: 0.2rem;
}

// Styling UserForm module's Reset button
form [type='reset'] {
    @include button(true, $secondary-color, $secondary-color, #fff, solid);

    width: auto;
    display: inline-block;
    margin-right: 0.2rem;
}

// Optional: Select Box styles
select {
    //background-image: url(../images/form-select.svg);
    background-image: none;
    background-size: 1.3rem;
    background-repeat: no-repeat;
    background-position: 100% center;
    padding-right: 0.5rem; // Cancels out the default Foundation right padding
}

// Prevent Textarea resizing horizontally
textarea {
    resize: vertical;
}

label {
    font-size: 1rem;
    font-weight: normal;

    input {
        margin-top: 0.2rem;
    }
}

legend {
    color: #000;
}

fieldset {
    padding: 0;
    border: none;
    margin-bottom: 1rem;
}

.checkbox {
    font-size: 1rem;
    margin-bottom: 1rem;
}

.field.file {
    padding: 1rem;
    background-color: transparent;
    color: $black;
    border: 1px solid $medium-gray;
    width: 100%;

    &:hover {
        background-color: transparent !important;
        color: inherit !important;
    }
}

.field.file label {
    text-shadow: none;
    text-transform: capitalize;
}

.field.file .button {
    background-color: transparent;
    padding: 0;
}

.field.file input {
    margin-bottom: 0;
}

// Required field's * style
.requiredField label:after {
    content: " * ";
    font-size: 1.2rem;
}

// Custom Checkbox & Radio Button Styles
[type="checkbox"]:checked + label:after,
[type="radio"]:checked + label:after {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
}

[type="checkbox"]:not(:checked) + label:after,
[type="radio"]:not(:checked) + label:after {
    opacity: 0;
    -webkit-transform: scale(0);
    transform: scale(0);
}

[type="checkbox"]:checked + label:before,
[type="radio"]:checked + label:before {
    background: #fff;
}

[type='checkbox'] + label,
[type='radio'] + label {
    margin-left: 0;
}

[type="checkbox"]:checked,
[type="checkbox"]:not(:checked),
[type="radio"]:checked,
[type="radio"]:not(:checked) {
    position: absolute;
    left: -9999px;
}

[type="checkbox"]:checked + label,
[type="checkbox"]:not(:checked) + label,
[type="radio"]:checked + label,
[type="radio"]:not(:checked) + label {
    position: relative;
    padding-left: 25px;
    cursor: pointer;
    line-height: 1.7;
    display: inline-block;
    color: $dark-gray;
}

[type="checkbox"]:checked + label,
[type="radio"]:checked + label {
    color: #000;
}

[type="checkbox"]:checked + label:before,
[type="checkbox"]:not(:checked) + label:before,
[type="radio"]:checked + label:before,
[type="radio"]:not(:checked) + label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 3px;
    width: 18px;
    height: 18px;
    background: url(../images/svg/icon-box.svg) no-repeat;
}

[type="checkbox"]:checked + label:after,
[type="checkbox"]:not(:checked) + label:after,
[type="radio"]:checked + label:after,
[type="radio"]:not(:checked) + label:after {
    content: '';
    width: 18px;
    height: 18px;
    background: url(../images/svg/icon-check.svg) no-repeat;
    position: absolute;
    left: 0;
    top: 3px;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

//[type="radio"]:checked + label:before,
//[type="radio"]:not(:checked) + label:before {
//    background: url(../images/form-unchecked.svg) no-repeat;
//}
//
//[type="radio"]:checked + label:after,
//[type="radio"]:not(:checked) + label:after {
//    background: url(../images/form-filled.svg) no-repeat;
//}

// Accessibility: Focus Outline
[type="checkbox"]:focus + label:before,
[type="radio"]:focus + label:before {
    outline: $secondary-color auto 5px;
}

// Messages Styles
.message {
    background: lighten($warning-color, 45%);
    padding: 0.5rem 1rem;
    border: 1px solid lighten($warning-color, 35%);
}

.message.error {
    background: lighten($alert-color, 40%);
    border: 1px solid lighten($alert-color, 30%);
    color: darken($alert-color, 10%);
}

.message.success {
    background: lighten($success-color, 40%);
    border: 1px solid lighten($success-color, 30%);
    color: darken($success-color, 40%);
}

form .middleColumn {
