form .Actions .action {
    font-size: 14px;
    text-transform: uppercase;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 0;
    padding: 10px 15px;
    width: 100%;
    min-height: 35px;
    background: $black;
    color: $white;
    border: 0;
    cursor: pointer;
    font-weight: bold;

    &:hover {
        background: $button-hover-bg;
    }
}

form .file .middleColumn .file.button {
    margin-bottom: 0;
}

form .file.button {
    margin-bottom: 30px;
    height: 40px;
}

form .file.button label {
    text-transform: uppercase;
}

form .file,
form .file.button {
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
    height: auto;
    border: 0;
}

form .file .file,
form .file.button .file {
    height: 40px;
}

form .file .middleColumn,
form .file.button .middleColumn {
    position: relative;

    @include transition;

    &:hover .file {
        &:before {
            background: $button-hover-bg;
        }
    }
}

form .file .middleColumn .file,
form .file.button .middleColumn .file {
    padding: 7px 0 7px 30px;
    color: $file-title;

    &:before {
        content: 'Choose File';
        color: $white;
        background: $black;
        position: absolute;
        left: 0;
        top: 0;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        height: 40px;
        width: 132px;
        font-size: 14px;

        @include transition;
    }
}

