.card {
    border-bottom: 1px solid $border-light-gray;
    margin-bottom: 25px;
    position: relative;

    &:before {
        content: '';
        position: absolute;
        top: -15px;
        left: -15px;
        width: calc(100% + 30px);
        height: calc(100% + 16px);
        border-bottom: 1px solid $border-light-gray;
        background: rgba(191, 189, 186, 0.4);

        @extend .fade-out;
    }

    &:hover,
    &:focus {
        &:before {
            @extend .fade-in;
        }
    }

    &:hover {
        .card__image .image {
            transform: scale(1.1);
        }
    }
}

.card__image {
    position: relative;
    overflow: hidden;
    display: block;

    &:before {
        content: '';
        width: 100%;
        padding-top: 70.98%;
        display: block;
    }
}

.card__image .image {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 0;

    @include bg-image;
    @include transition-image;
}

.card__image .link {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    font-size: 15px;
    line-height: 1.2;
    font-weight: bold;
    text-align: center;
    color: $white;
    background: rgba(0, 0, 0, 0.37);
    padding: 10px 15px;
    display: none;

    @include transition;

    //&:hover {
    //    background: $black;
    //}
}

.card__content {
    position: relative;
    padding: 20px 0 15px;
}

.card__content .title {
    display: block;
    font-size: 20px;
    margin-bottom: 15px;
    color: $black;

    @extend .Brown-Bold-web;
}

.card__content .details {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.card.blue-highlight {
    .card__content .details .price,
    .card__content .details .price:before {
        color: $dark-blue;
    }
}

.card__content .details .features {
    display: flex;
    align-items: center;
}

.card__content .details .features .feature-item {
    display: flex;
    align-items: center;
    margin-right: 15px;
}

.card__content .details .features .feature-item {
    &:last-child {
        margin-right: 0;
    }
}

.card__content .details .features .feature-item img {
    width: 22px;
    max-width: 100%;
    margin-right: 10px;
}

.card__content .details .features .feature-item .count {
    color: $border-light-gray;
    font-size: 20px;
    font-weight: bold;
}

.card__content .details .price {
    font-size: 20px;
    font-weight: bold;
    color: $orange;
}

.card__content .details .price:before {
    content: '$';
    font-size: 20px;
    font-weight: bold;
    color: $orange;
    display: inline-block;
}

@include breakpoint(medium) {
    .card__image .link {
        display: block;
    }
}

@include breakpoint(large) {
    .narrow .card__content .title {
        font-size: 16px;
    }

    .narrow .card__content .details .features .feature-item {
        margin-right: 10px;
    }

    .narrow .card__content .details .features .feature-item img {
        width: 18px;
        margin-right: 5px;
    }

    .narrow .card__content .details .features .feature-item .count,
    .narrow .card__content .details .price {
        font-size: 16px;
    }

    .narrow .card__content .details .price {
        &:before {
            font-size: 16px;
        }
    }
}
