.pagination-bar {
    margin-top: 30px;
}

.pagination-bar .pagination {
    position: relative;
    margin-bottom: 0;
}

.pagination-bar .pagination li {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 12px;

    @include transition;
}

.pagination-bar .pagination li a {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 12px;
}

.pagination-bar .pagination li {
    &:hover {
        background: $primary-color;
        color: $white;

        a {
            background: $primary-color;
            color: $white;
            cursor: pointer;
        }
    }
}

.pagination-bar .pagination li.current {
    background: $primary-color;
    color: $white;
    cursor: not-allowed;
}

.pagination-bar .pagination li.disabled {
    &:hover {
        background: transparent;
        color: $tabs-hover;

        a {
            background: transparent;
            color: $tabs-hover;
        }
    }
}

.pagination-bar .pagination-next.disabled {
    &:after {
        display: none;
    }
}

.pagination-bar .pagination li.pagination-next,
.pagination-bar .pagination li.pagination-previous {
    position: absolute;
}

.pagination-bar .pagination li.pagination-previous {
    left: 0;

    &:before {
        display: none;
    }
}

.pagination-bar .pagination li.pagination-previous svg {
    margin-right: 10px;
}

.pagination-bar .pagination li.pagination-next {
    right: 0;
}

.pagination-bar .pagination li.pagination-next a {
    &:after {
        display: none;
    }
}

.pagination-bar .pagination li.pagination-next a svg {
    margin-left: 10px;
}

@include breakpoint(xlarge) {
    .pagination-bar .pagination li {
        font-size: 15px;
    }

    .pagination-bar .pagination li a {
        font-size: 15px;
    }
}
