.listing-maps {
    &__video {
        margin-bottom: 30px;

        .video-wrapper {
            margin-bottom: 40px;
            position: relative;
            width: 100%;
            max-width: 600px;

            &:before {
                content: '';
                width: 100%;
                display: block;
                padding-top: 56.25%;
            }
        }

        iframe {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
        }
    }

    &__maps {
        margin-bottom: 40px;

        &:last-child {
            margin-bottom: 0;
        }

        .title-wrapper {
            position: relative;
            margin-bottom: 20px;
            min-height: 25px;
            display: flex;
            align-items: center;

            .title {
                text-transform: uppercase;
                color: $border-light-gray;
                margin-bottom: 0;

                @extend .Brown-Bold-web;
            }

            .icon-zoom {
                position: absolute;
                right: 0;
                top: 0;
                width: 25px;
                cursor: pointer;
            }

            .icon-zoom img {
                width: 100%;
            }
        }

        .align-map {
            img {
                width: 100%;
            }
        }
    }

    @include breakpoint(large) {
        &__video {
            .video-wrapper {
                width: 85%;
            }
        }

        &__maps {
            .title-wrapper .title {
                font-size: 20px;
            }
        }
    }

    @include breakpoint(xlarge) {
        &__video {
            .video-wrapper {
                width: 74%;
            }
        }
    }

    @include breakpoint(xxlarge) {
        &__video {
            .video-wrapper {
                width: 100%;
            }
        }
    }
}
