.carousel-card {
    position: relative;
    padding: 0 35px 50px;
}

.carousel-card.blue-highlight .carousel-dots .slick-dots .slick-active button {
    background: $dark-blue;
}

.carousel-card.blue-highlight .carousel-dots .slick-dots button {
    &:hover {
        background: $dark-blue;
    }
}

.carousel-card .slider-item {
    position: relative;

    &:before {
        content: '';
        width: 100%;
        padding-top: 62.5%;
        display: block;
    }
}

.carousel-card .slider-item .image {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 0;

    @include bg-image;
}

.carousel-card .carousel-arrows {
    position: absolute;
    width: 100%;
    height: 20px;
    left: 0;
    top: calc(50% - 25px);
    transform: translate(0, -50%);
}

.carousel-card .carousel-arrows .slick-next {
    right: 45px;
}

.carousel-card .carousel-arrows .slick-prev {
    left: 45px;
}

.carousel-card .carousel-arrows .slick-arrow {
    cursor: pointer;
}

.carousel-card .carousel-dots {
    position: absolute;
    width: 100%;
    height: 10px;
    left: 0;
    bottom: 10px;
}

@include breakpoint(medium) {
    .carousel-card {
        padding: 0 0 50px;
    }

    .carousel-card .carousel-arrows .slick-next {
        right: 20px;
    }

    .carousel-card .carousel-arrows .slick-prev {
        left: 20px;
    }
}

@include breakpoint(large) {
    .carousel-card .slider-item:before {
        padding-top: 83.42%;
    }

    .carousel-card .carousel-arrows {
        height: 38px;
    }
}
